.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color:rgba(0, 0, 0, .2);
    display: flex;
    justify-content: center;
    align-items: center;
}

.popupInner {
    position: relative;
    padding: 32px;
    width: 100%;
    max-width: 352px;
    background-color: #fbfbfb;
    color: #2b2b2b;
    display: grid;
    grid-auto-flow: column;
    border-radius: 5px;
    box-sizing: border-box;
}

.popupInner ul {
    list-style-type: none;
    padding-left: 0px;
    width: 100%;
    grid-column: 1;
    color: #680148;
}

.popupInner label {
    grid-column: 1;
    display: grid;
    grid-template-columns: 33% 1fr;
}

.popupInner input {
    grid-column: 2;

}

.popupInner select {
    grid-column: 2;

}

.popupInner p {
    grid-column: 1;
}

.popupInner #button_div {
    grid-column: 1;
    display: flex;
    justify-content: flex-end;
}

.popupInner #button_div button {
    border-radius: 5px;
    border: none;
    background-color: #000000;
    color: #E0EFF1;
    min-height: 44px;
    margin-top: .5rem;
}

#right_most_button {
    margin-left: .5rem;
}

.popupInner textarea {
    font-family: Arial;
    resize: none;
}

@media screen and (max-width: 315px) {
    .popupInner input {
        grid-column: 1;
    
    }
    
    .popupInner select {
        grid-column: 1; 
    }

    .popupInner textArea {
        grid-column: 1;
    }

    .popupInner label {
        grid-column: 1;
        display: grid;
        grid-template-columns: 1fr;
    }
}