.popup {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.popup_inner {
    position: relative;
    background-color: white;
    box-sizing: border-box;
    border-radius: 5px;
    padding: 16px 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.popup_inner form {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
}

.popup_inner form label {
    display: flex;
    flex-direction: column;
    margin-bottom: .25rem;
}

#button_div {

    display: flex;
    justify-content: flex-end;

}

#button_div button {
    border-radius: 5px;
    border: none;
    background-color: #000000;
    color: #E0EFF1;
    min-height: 44px;
    margin-top: .5rem;
}

.popup_inner a {
    padding-top: .75rem;
    font-size: .75rem;
    cursor: default;
    margin: 0 auto;
}

.popup_inner a:hover {
    color: #680148;
}

.popup_inner ul {
    list-style-type: none;
    padding-left: 0px;
    max-width: 25rem;

    color: #680148;
   
}

.popup_inner ul li {
    margin-bottom: .5rem;
}

#show_password_checkbox {
    align-self: flex-start;
    display: flex;
    font-size: .75rem;
    align-items: center;
    accent-color: black;
}

#show_password_checkbox input {
    margin-left: 0rem;
}

#show_password_checkbox label {
    margin-bottom: 0rem;
}



